div#portfolio {
  overflow: hidden;
}
.portfolio-nav {
  position: absolute;
  font-size: 30px;
  top: 48%;
}
.portfolio-nav.left {
  left: 40px;
}
.portfolio-nav.right {
  right: 36px;
}
.projects {
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  min-height: 260px;
}
.project {
  width: 100%;
  min-height: 250px;
  text-align: center;
}
.project-image {
  min-height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.project-image img {
  border-radius: 20%;
  width: 160px;
  height: 160px;
  object-fit: cover;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.project-image img.loading {
  opacity: 0;
}
.project-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.project-info h2 {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
}
.project-info p {
  font-size: 12px;
  height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin: 10px 20px;
}
.project-counter {
  font-size: 12px;
  margin-top: 10px;
  color: #666;
  font-style: italic;
}
@media screen and (max-width: 540px) {
  .portfolio-nav {
    font-size: 40px;
    top: 42%;
  }
  .project {
    min-height: 200px;
  }
  .project-image {
    min-height: 140px;
  }
  .project-image img {
    width: 120px;
    height: 120px;
  }
  .project-info h2 {
    font-size: 14px !important;
    height: 25px;
  }
  .project-info p {
    height: 50px;
    -webkit-line-clamp: 2;
    margin: 5px 15px;
  }
  .portfolio-nav i.icon {
    font-size: 0.6em;
  }
}
